@font-face {
  font-family: "BebasNeue-Regular";
  src:local("BebasNeue-Regular"),
    url("../fonts/bebas-neue-regular.woff2") format("woff2"),
    url("../fonts/bebas-neue-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "BebasNeue-Bold";
  src:local("BebasNeue-Bold"),
    url("../fonts/bebas-neue-bold.woff2") format("woff2"),
    url("../fonts/bebas-neue-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "RobotoCondensed-Light";
  src:local("RobotoCondensed-Light"),
    url("../fonts/roboto-condensed-light.woff2") format("woff2"),
    url("../fonts/roboto-condensed-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "RobotoCondensed-Regular";
  src:local("RobotoCondensed-Regular"),
    url("../fonts/roboto-condensed-regular.woff2") format("woff2"),
    url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "RobotoCondensed-Bold";
  src:local("RobotoCondensed-Bold"),
    url("../fonts/roboto-condensed-bold.woff2") format("woff2"),
    url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
